.app-wrapper header img {
    height: 50px;
}

.content img {
    height: 200px;
}

.app-wrapper {
    margin: 0 auto;
    width: 1200px;
    display: grid;

    grid-template-areas:
        "h h"
        "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
}

.app-wrapper-content {
    background-color: antiquewhite;
    grid-area: c;
    color: darkgreen;
}